<template>
  <el-container class="table-el-container">
    <el-header class="table-el-header">
      <el-page-header @back="goBack" content="资格设置"/>
    </el-header>
    <el-main class="table-el-main">
      <table>
        <thead>
          <tr>
            <th></th>
            <th v-for="(course, index) in headList" :key="index">{{course}}</th>
          </tr>
        </thead>
        <tbody>
          <!-- 高级资格 -->
          <tr>
              <td class="title">高级资格</td>
              <td colspan="5">
                <a href="#" @click="itemClick($event, 'senior')">信息系统项目管理师</a>
                <a href="#" @click="itemClick($event, 'senior')">系统分析师</a>
                <a href="#" @click="itemClick($event, 'senior')">系统架构设计师</a>
                <a href="#" @click="itemClick($event, 'senior')">网络规划设计师</a>
                <a href="#" @click="itemClick($event, 'senior')">系统规划与管理师</a>
              </td>
          </tr>
          <!-- 中级资格 -->
          <tr>
              <td class="title">中级资格</td>
              <td>
                <p><a href="#" @click="itemClick($event, 'middle')">软件评测师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">软件设计师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">软件过程能力评估师</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'middle')">网络工程师</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'middle')">多媒体应用设计师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">嵌入式系统设计师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">计算机辅助设计师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">电子商务设计师</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'middle')">系统集成项目管理工程师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">信息系统监理师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">信息安全工程师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">数据库系统工程师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">信息系统管理工程师</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'middle')">计算机硬件工程师</a></p>
                <p><a href="#" @click="itemClick($event, 'middle')">信息技术支持工程师</a></p>
              </td>
          </tr>
          <!-- 初级资格 -->
          <tr>
              <td class="title" @click="itemClick($event, 'junior')">初级资格</td>
              <td>
                <p><a href="#" @click="itemClick($event, 'junior')">程序员</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'junior')">网络管理员</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'junior')">多媒体应用制作技术员</a></p>
                <p><a href="#" @click="itemClick($event, 'junior')">电子商务技术员</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'junior')">信息系统运行管理员</a></p>
              </td>
              <td>
                <p><a href="#" @click="itemClick($event, 'junior')">网页制作员</a></p>
                <p><a href="#" @click="itemClick($event, 'junior')">信息处理技术员</a></p>
              </td>
          </tr>
        </tbody>
      </table>
      <p>*&nbsp;点击表格内的专业查看具体考试说明</p>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'GuideTable',
  data() {
    return {
      detailInfoList: '',
      headList: ['计算机软件', '计算机网络', '计算机应用技术', '信息系统', '信息服务']
    }
  },
  props: {
    ccdetailInfoList: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
      this.$emit('goback')
    },
    itemClick(e, val) {
      this.detailInfoList = this.getDetaiInfo(this.getLevelInfo(val), e.target.innerHTML)
      this.$emit('itemClick', this.detailInfoList)
    },
    getLevelInfo(val) {
      let data = ''
      switch(val) {
        case 'junior':
          data = this.ccdetailInfoList.junior
          break;
        case 'middle':
          data = this.ccdetailInfoList.middle
          break;
        case 'senior':
          data = this.ccdetailInfoList.senior
          break;
      }
      return data
    },
    getDetaiInfo(val, direction) {
      for(let i of val) {
        if(i.direction === direction) {
          return i
        }
      }
    }
  }
}
</script>

<style>
  .table-el-main {
    margin: 0 32px;
  }
  .table-el-main table {
    border-collapse: collapse;
    text-align: center;
  }
  .table-el-main th {
    padding: 16px 12px;
    border: 1px solid #000;
    line-height: 51px;
  }
  .table-el-main td {
    padding: 16px 12px;
    border: 1px solid #000;
  }
  .table-el-main a {
    color: #000;
    text-decoration: none;
    margin: 0 12px;
  }
  .table-el-main p {
    color: red;
  }
</style>