<template>
  <el-container class="detail-el-container">
    <el-header class="detail-el-header">
      <el-page-header @back="goBack" content="考试说明"/>
    </el-header>
    <el-main class="detail-el-main">
      <div class="detail-el-main-content">
        <h2>{{ccurrentDetailInfo.direction}}考试说明</h2>
        <div>
          <span>{{ccurrentDetailInfo.date}}</span>
          <span>来源: {{ccurrentDetailInfo.source}}</span>
          <span>【字体:
            <a href="#" @click="updataFontSize('big')">大</a> 
            <a href="#" @click="updataFontSize('middle')">中</a> 
            <a href="#" @click="updataFontSize('small')">小</a>】</span>
        </div>
        <el-divider/>

        <div 
        class="detail-info" 
        :class="{bfontsize:currentFontSize === 1, mfontsize:currentFontSize === 2 , sfontsize:currentFontSize === 3}">
          <p>{{this.ccurrentDetailInfo.explain}}</p>
        </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'Detail',
  data() {
    return {
      currentFontSize: ''
    }
  },
  props: {
    ccurrentDetailInfo: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
      this.$emit('goback')
    },
    updataFontSize(size) {
      switch(size) {
        case 'big':
          this.currentFontSize = 1
          break;
        case 'middle':
          this.currentFontSize = 2
          break;
        case 'small':
          this.currentFontSize = 3
          break;
      }
    }
  }
}
</script>

<style>
  .detail-el-header {
    line-height: 60px;
  }
  .detail-el-main {
    margin: 0 32px;
  }
  .detail-el-main-content {
    text-align: center;
  }
  .detail-el-main-content h2 {
    font-size: 20px;
  }
  .detail-el-main-content span {
    padding: 0 10px;
  }
  .detail-el-main-content a {
    margin: 0 2px;
    text-decoration: none;
    color: black;
  }
  .detail-info p {
    white-space: pre-line;
    line-height: 2;
  }
  .detail-info {
    text-align: left;
  }
  .bfontsize {
    font-size: 18px;
  }
  .mfontsize {
    font-size: 16px;
  }
  .sfontsize {
    font-size: 14px;
  }
</style>