<template>
  <div>
    <guide-table 
    :ccdetailInfoList="cdetailInfoList"
    @itemClick="tableClick"
    @goback="isDetail"
    v-show="isShow"
    />
    <guide-detail
    :ccurrentDetailInfo="currentDetailInfo"
    @goback="isDetail"
    v-show="!isShow"/>
  </div>
</template>

<script>
import GuideTable from './childComps/GuideTable'
import GuideDetail from './childComps/GuideDetail'
export default {
  name: 'MainGuide',
  data() {
    return {
      courseList: [],
      detailInfoList: {},
      currentDetailInfo: {},
      isShow: true
    }
  },
  props: {
    cdetailInfoList: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  components: {
    GuideTable,
    GuideDetail
  },
  methods: {
    tableClick(item) {
      this.isShow = !this.isShow
      this.currentDetailInfo = item
    },
    isDetail() {
      this.isShow = !this.isShow
    }
  }
}
</script>

<style>
  .guide-el-main {
    margin: 0 32px;
  }
  .el-page-header__left::after {
    background-color: #aaa;
  }

  
  .el-divider {
    background-color: rgba(125, 125, 125, 0.4);
  }
</style>